import { Map, List } from 'immutable';

/**
 * 
 */
export default class Utils {

    // 
    static pollFuns = []

    // 
    static poll = (() => {
        // 1
        setInterval(function() {
            // 
            for (let i in Utils.pollFuns) {
                // 
                let obj = Utils.pollFuns[i];
                // 
                if (typeof obj === 'function') {
                    // 
                    obj();
                // 
                } else {
                    // 
                    obj.cur = (obj.cur || obj.delay) - 1;
                    // 
                    if(obj.cur < 1) {
                        //  func 
                        if(typeof obj.func === 'function') {
                            // 
                            obj.func();
                        }
                        // 
                        obj.cur = obj.delay;
                    }
                }
            }
        }, 1000);
    })();

    /**
     * [pushPollFuns ] todo 
     * @param  {[type]} obj [ function  {func: ()=> {}, delay: 10}]
     * @return {[type]}      [description]
     */
    static pushPollFuns(obj) {
        // func 
        let func = obj;
        // obj 
        if (typeof obj === 'object') {
            //  func 
            func = obj.func;
        }
        // 
        if (typeof func === 'function') {
            //  
            Utils.pollFuns.push(obj);
        }
    }

    // key
    static key = 0

    /**
     * key <React key={Utils.getKey('react')} />
     * @param  { key }
     * @return {[react_1 key]}
     */
    static getKey(prefix) {
        // 
        let p = (prefix || 'key') + '_';
        // key 1
        Utils.key = Utils.key + 1;
        // key
        return p + Utils.key;
    }

    /**
     * [uuid uuid]
     * @return {[type]} [description]
     */
    static uuid() {
        // uuid
        return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
            var r = Math.random() * 16 | 0,
                v = c == 'x' ? r : (r & 0x3 | 0x8);
            return v.toString(16);
        });
    }

    /**
     * 
     * @param  {[type]}  obj 
     * @return {Boolean}     [description]
     */
    static isEmpty(obj) {
        if (!obj) return true;
        if (this.isArray(obj)) {
            return obj.length == 0;
        } else if (this.isObject(obj)) {
            for (var name in obj) {
                return false;
            }
            return true;
        }
        return false;
    };

    /**
     * 
     * @param  {[type]}  obj 
     * @return {Boolean}     [description]
     */
    static isObject(obj) {
        var type = typeof obj;
        return type === 'function' || type === 'object' && !!obj;
    };

    /**
     * 
     * @param  {[type]}  obj 
     * @return {Boolean}     [description]
     */
    static isArray(obj) {
        return Array.isArray(obj) || toString.call(obj) === '[object Array]';
    };

}



